footer {
  padding-top: 6vh;
  background-color: rgb(0, 25, 55);
}
#app__footer_navigations {
  display: grid;
  grid-template-columns: 
    calc(100% / 4 - 1em) calc(100% / 6 - 1em)
    calc(100% / 6 - 1em) calc(100% / 6 - 1em)
    calc(100% / 6 - 1em) calc(100% / 6 - 1em)
  ;
  grid-gap: 1em;
  gap: 1em;
  padding-bottom: 4vh;
}

@media screen and (max-width: 1024px) {
  #app__footer_navigations {
    grid-template-columns: 
    calc(100% / 5 - 1em) calc(100% / 5 - 1em)
    calc(100% / 5 - 1em) calc(100% / 5 - 1em)
    calc(100% / 5 - 1em) calc(100% / 5 - 1em);
  }
  #app__footer_short_contact {
    grid-column-start: 1;
    grid-column-end: 7;
  }
}
@media screen and (max-width: 768px) {
  #app__footer_navigations {
    grid-template-columns: 
    calc(100% / 3 - 1em)
    calc(100% / 3 - 1em)
    calc(100% / 3 - 1em);
  }
  #app__footer_short_contact {
    grid-column-start: 1;
    grid-column-end: 4;
  }
}
@media screen and (max-width: 478px) {
  #app__footer_navigations {
    grid-template-columns: 
    calc(100% / 2 - 1em)
    calc(100% / 2 - 1em);
  }
  #app__footer_navigations
  #app__footer_short_contact {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}
