/**

  Save your money banner styles

**/
#app__home_save_your_money {
  margin-top: 10em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 500px;
  background-color: white;
  background-image: url('/assets/images/banners/banner_save_money_on_your_tax.PNG');
  background-repeat: no-repeat;
  background-size: contain;
  background-position-x: 50%;
  color: var(--font-color-black);
}
@media screen and (min-width: 900px) {
  #app__home_save_your_money {
    background-size: cover;
  }
}
#app__home_save_your_money #title {
  color: #001a38;
  font-family: Inter,sans-serif;
  font-size: 1.75em;
  font-weight: 500;
  line-height: 54.6px;
  margin: 0;
  margin-bottom: 1em;
}
#app__home_save_your_money #subtitle {
  margin-bottom: 40px;
  font-size: 1.25em;
  line-height: 1.8;
}
#app__home_save_your_money #app__common_btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
}
@media screen and (max-width: 768px) {
  #app__home_save_your_money {
    padding-top: 2em;
    padding-bottom: 2em;
    margin: auto;
    margin-top: 5em;
    margin-bottom: 5em;
    width: 90%;
    min-height: 250px;
    background-size: 125% 100%;
  }
  #app__home_save_your_money #title {
    font-size: 1.55em;
    line-height: 35px;
  }
  #app__home_save_your_money #subtitle {
    margin-top: 1em;
    font-size: 1.2em;
  }
}
/**

  Easily Import banner style

**/
#app__home_easily_import {
  margin-top: 10em;
  margin-bottom: 0em;
  padding-top: 2em;
  padding-bottom: 2em;
}
#app__home_easily_import > .container {
  display: grid;
  grid-template-columns: calc(50% - 1em) calc(50% - 1em);
  grid-gap: 1em;
  gap: 1em;
}
#app__home_easily_import_banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#app__home_easily_import_banner img {
  width: 100%;
}
#app__home_easily_import_content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
#app__home_easily_import_content #title {
  color: #001a38;
  font-family: Inter,sans-serif;
  font-size: 42px;
  font-weight: 500;
  line-height: 54.6px;
  margin: 0;
  margin-bottom: 0em;
}
#app__home_easily_import_content #subtitle {
  margin-bottom: 40px;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.8;
}
@media screen and (max-width: 768px) {
  #app__home_easily_import {
    margin-top: 5em;
  }
  #app__home_easily_import > .container {
    grid-template-columns: 100%;
  }
  #app__home_easily_import_banner {
    margin: auto;
    width: 80%;
    height: 80%;
    grid-row-start: 2;
  }
  #app__home_easily_import_banner img {
    width: 100%;
  }
}
