/**

  Sliders styles

**/
#app__home_platform_sliders {
  margin-top: 10em;
}
#app__home_platform_header {
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
}
#app__home_platform_header #title {
  color: #001a38;
  font-family: Inter,sans-serif;
  font-size: 42px;
  font-weight: 500;
  line-height: 54.6px;
  margin: 0;
  margin-bottom: 1em;
}
#app__home_platform_sliders_content {
  margin-top: 3em;
  display: flex;
  flex-direction: column;
}
.app__home_platform_slider_wrapper {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.app__home_platform_slider_carousel {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 5%;
  width: 100%;
  height: 90%;
}
.app__home_platform_slider_carousel.left {
  transform: translateX(0%);
  animation: SliderAnimationToLeft 200s linear infinite;
}
.app__home_platform_slider_carousel.right {
  transform: translateX(-1440%);
  animation: SliderAnimationToRight 200s linear infinite;
}
@keyframes SliderAnimationToLeft {
  0% { transform: translateX(-1440%); }
  100% { transform: translateX(0%); }
}
@keyframes SliderAnimationToRight {
  0% { transform: translateX(0%); }
  100% { transform: translateX(-1540%); }
}
.app__home_platform_slider_block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 20%;
}
@media screen and (max-width: 1024px) {
  .app__home_platform_slider_block {
    min-width: 25%;
  }
}
@media screen and (max-width: 768px) {
  .app__home_platform_slider_block {
    min-width: 30%;
  }
}
@media screen and (max-width: 600px) {
  .app__home_platform_slider_block {
    min-width: 35%;
  }
}
.app__home_platform_slider_item {
  cursor: pointer;
  margin: auto;
  padding: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  text-align: center;
  border: 1px solid rgba(175, 175, 175, 0.25);
  border-radius: 6px;
  width: calc(90% - 2em);
  height: calc(90% - 2em);
  transition: .2s ease-in-out;
}
.app__home_platform_slider_block:hover 
.app__home_platform_slider_item {
  box-shadow: 0px 0px 16px rgba(90, 90, 90, 0.5);
}
.app__home_platform_slider_item #icon {
  margin: auto;
  width: 80%;

}
.app__home_platform_slider_item #icon img {
  width: 45%;
}
@media screen and (max-width: 1024px) {
  .app__home_platform_slider_item #icon img {
    width: 55%;
  }
}
@media screen and (max-width: 768px) {
  .app__home_platform_slider_item #icon img {
    width: 65%;
  }
}
@media screen and (max-width: 600px) {
  .app__home_platform_slider_item #icon img {
    width: 85%;
  }
}
.app__home_platform_slider_item #title {
  width: 100%;
  max-width: 142px;
  color: #203855;
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  margin: auto;
}
@media screen and (max-width: 768px) {
  #app__home_platform_sliders {
    margin-top: 5em;
  }
}
