  position: relative;
  height: 18px;
  width: 18px;
  border-radius: 100%;
  background-color: lightgreen;
}
#app__home_calculate_banner_content_list li span::before,
#app__home_calculate_banner_content_list li span::after {
  content: '';
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  background-color: white;
  height: 2px;
}
#app__home_calculate_banner_content_list li span::before {
  width: 6px;
  top: 67.5%;
  left: 27.5%;
  rotate: 45deg;
}
#app__home_calculate_banner_content_list li span::after {
  width: 10px;
  top: 30%;
  left: 55%;
  rotate: -45deg;
}
#app__home_calculate_banner_content > .app__common_btn {
  cursor: pointer;
}
