@media screen and (max-width: 1024px) {
  #app__page_auth {
    display: flex;
    flex-direction: column;
    grid-template-columns: 100%;
  }
}

#app__page_auth .app__auth_register_block,
#app__page_auth .app__auth_reviews_block {
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 1024px) {
  #app__page_auth .app__auth_register_block,
  #app__page_auth .app__auth_reviews_block {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #app__page_auth .app__auth_reviews_block {
