/**

  Recovery block

**/
#app__register_form_seed {
  margin: auto;
  width: 80%;
}
#app__register_form_seed #header {
  text-align: center;
}
#app__register_form_seed #title {
  font-size: 1.25rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  margin: 0;
}
#app__register_form_seed #subtitle {
  font-size: .8rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  margin: 0;
  margin-top: 1em;
  margin-bottom: 1em;
}
#app__seed_length_select {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media screen and (max-width: 500px) {
  #app__seed_length_select {
    flex-direction: column;
    text-align: left;
    grid-gap: .5em;
    gap: .5em;
    margin: auto;
  }
}
#app__seed_length_select label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 1rem;
}
#app__seed_length_select
#app__seed_length_selection {
  padding: .5em;
  background: transparent;
  border: 1px solid rgba(45, 45, 45, 0.5);
  border-radius: 4px;
}
#app__seed_form_block {
  margin: auto;
  margin-top: 1em;
  margin-bottom: 1em;
  display: grid;
  grid-template-columns: calc(100% / 3 - 1em) calc(100% / 3 - 1em) calc(100% / 3 - 1em);
  grid-gap: 1.5em;
  gap: 1.5em;
}
.app__seed_form_cell {
  width: 100%;
  display: flex;
  grid-gap: .5em;
  gap: .5em
}
.app__seed_form_cell label {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.app__seed_form_cell input {
  width: inherit;
}
.app__seed_form_cell input.invalid {
  border: 2px solid red;
}
/**

  Registartion block styles

**/
.app__auth_register_block {
  position: relative;
  background-color: white!important;
}
#app__auth_register_logotype {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

#app__auth_register_block_form {
  margin: auto;
  padding-top: 1em;
  padding-bottom: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  min-width: 300px;
  max-width: 450px;
  min-height: 300px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#app__register_form_title {
  text-align: center;
}
#app__register_form_title h1 {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
}
#app__register_form_controls {
  margin: auto;
  width: 80%;
  display: grid;
  grid-template-columns: 50% 50%;
}
#app__register_form_controls .app__register_form_control {
  display: flex;
  width: -webkit-fill-available;
  width: -moz-available;
  grid-column-start: 1;
  grid-column-end: 3;
  margin-top: .25em;
  margin-bottom: .25em;
}
#app__register_form_controls .app__register_form_control:nth-child(5) {
  display: flex;
  grid-column-start: 1;
  grid-column-end: 2;
  margin-right: .5em!important;
}
#app__register_form_controls .app__register_form_control:nth-child(6) {
  display: flex;
  grid-column-start: 2;
  grid-column-end: 3;
  margin-left: .5em!important;
}
#app__auth_register_block_form .app__register_form_control label {
  display: flex;
  flex-direction: column;
  grid-gap: .25em;
  gap: .25em;
  text-align: start;
  width: 100%;
}
#app__auth_register_block_form .app__register_form_control label span {
  color: rgb(125, 125, 125);
  font-size: clamp(10px, 12px, 14px);
  font-weight: 600;
  font-family: 'Roboto', sans-serif;
}
#app__auth_register_block_form .app__register_form_control label span a {
  color: var(--font-color-blue);
  text-decoration: none;
}
#app__auth_register_block_form .app__register_form_control label span a:hover {
  text-decoration: underline;
}
#app__auth_register_block_form .app__register_form_control input[type=text],
#app__auth_register_block_form .app__register_form_control input[type=email],
#app__auth_register_block_form .app__register_form_control input[type=date],
#app__auth_register_block_form .app__register_form_control input[type=password] {
  width: -webkit-fill-available;
  width: -moz-available;
  padding: 0.5em;
  border: 1px solid rgba(125, 125, 125, 0.5);
  border-radius: 4px;
  outline: none;
  -webkit-appearance: none;
          appearance: none;
}
#app__auth_register_block_form .app__register_form_control input[type=text].filled,
#app__auth_register_block_form .app__register_form_control input[type=email].filled,
#app__auth_register_block_form .app__register_form_control input[type=date].filled,
#app__auth_register_block_form .app__register_form_control input[type=password].filled {
  background-color: rgb(102 198 233 / 25%);
}
#app__auth_register_block_form .app__register_form_control input[type=text]:focus,
#app__auth_register_block_form .app__register_form_control input[type=email]:focus,
#app__auth_register_block_form .app__register_form_control input[type=date]:focus,
#app__auth_register_block_form .app__register_form_control input[type=password]:focus {
  border: 2px solid rgb(100, 175, 255);
  box-shadow: 0px 0px 4px rgba(100, 175, 255, 0.5);
}
#app__auth_register_block_form .app__register_form_control input[type=text].invalid,
#app__auth_register_block_form .app__register_form_control input[type=email].invalid,
#app__auth_register_block_form .app__register_form_control input[type=date].invalid,
#app__auth_register_block_form .app__register_form_control input[type=password].invalid {
  border: 2px solid red;
}
#app__auth_register_block_form #app__register_form_checkbox_control {
  margin: auto;
  width: 80%;
  display: flex;
  flex-direction: row;
}
#app__auth_register_block_form #app__register_form_checkbox_control label span {
  margin: auto;
  margin-left: 1em;
}
#app__auth_register_block_form #auth__form_submit{
  margin: auto;
  width: 80%;
}
#app__auth_register_block_form #auth__form_submit button {
  cursor: pointer;
  padding: 0.75em;
  width: 100%;
  border: none;
  border-radius: 6px;
  background-color: var(--background-blue);
  color: white;
  font-size: 14px;
  font-weight: 600;
  transition: .1s ease-in-out;
}
#app__auth_register_block_form #auth__form_submit button:hover { 
  background-color: var(--background-hover-blue);
}
#app__auth_register_block_form #auth__form_submit button:disabled {
  cursor: auto;
  background-color: rgba(108, 129, 131, 0.5);
}

/**

  Reviews block styles

**/

.app__auth_reviews_block {
  display: flex;
  flex-direction: column;
  background-color: rgb(0, 25, 60);
}
#app__auth_reviews_block_content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#app__reviews_block_title {
  text-align: center;
}
#app__reviews_block_title h1 {
  margin: .25em;
  color: whitesmoke;
  font-size: 22px;
}
#app__reviews_block_title p {
  color: rgb(175, 175, 175);
  font-size: 14px;
}

#app__reviews_block_cards {
  margin: auto;
  margin-top: 1em;
  margin-bottom: 1em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  grid-gap: .5em;
  gap: .5em;
}

.app__reviews_block_card_block {
