}

#app__reviews_block_footer_title {
  text-align: center;
  font-weight: 600;
}
#app__reviews_block_footer_title p {
  color: whitesmoke;
  font-size: 14pxvh;
}
#app__reviews_block_footer_title p span {
  color: var(--font-color-blue);
}
#app__reviews_block_footer_images {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
